<template>
  <div>
    <div style="float: left;vertical-align:middle; text-align: left">
      <img src="@/assets/logol.svg"/>
    </div>

    <div style="float: left;vertical-align:middle; text-align: left">
    <el-menu :default-active="this.$router.path" class="el-menu-demo" style="border: hidden" router mode="horizontal" background-color="#303133" text-color="#fff"
             active-text-color="#ffd04b"  >
      <el-menu-item v-for="(item,i) in navList" :key="i" :index="item.name">
          {{ item.navItem }}
      </el-menu-item>
    </el-menu>
    </div>
    <div style="float: right;vertical-align:middle; " >
      <el-dropdown  trigger="click">
        <el-button icon="el-icon-bell" type="text" style=" background-color: #303133;color: floralwhite;font-size: 20px;font-weight: bold"  circle></el-button>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>1</el-dropdown-item>
          <el-dropdown-item>1</el-dropdown-item>
          <el-dropdown-item>2</el-dropdown-item>
          <el-dropdown-item>2</el-dropdown-item>
          <el-dropdown-item>3</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <el-button type="text"  @click="dialogFormVisible = true" style="color: floralwhite;font-weight: bold">
        
      </el-button>
      <el-dialog title="" :visible.sync="dialogFormVisible">
        <el-form :model="form">
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="form.name" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input v-model="form.passwd" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false"> </el-button>
          <el-button type="primary" @click="dialogFormVisible = false"> </el-button>
        </div>
      </el-dialog>
      <el-button type="text" style="color: floralwhite" >
        
      </el-button>

    </div>
  </div>
</template>

<script>

  export default {
		data() {
      return {
        navList:[
            {name:'/',navItem:''},
            {name:'/Document',navItem:''},
            {name:'/Download',navItem:''},
            {name:'/About',navItem:''},
            {name:'/Control',navItem:''},
        ],
        dialogFormVisible: false,
        form: {
          name: '',
          passwd:'',
          region: '',
          date1: '',
          date2: '',
          delivery: false,
          type: [],
          resource: '',
          desc: ''
        },
        formLabelWidth: '120px'
      }
    },

    //
    computed:{
      activeIndex(){
        return this.$route.path.replace('/','')
      }
    },
    methods: {
      handleSelect(key, keyPath) {
        console.log(this);
        console.log(key,keyPath);
      },
      open2() {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      }

    }

  }

</script>




