<template>
  <div>
    <!--<div class="whiteFont">-->
      <!--<span style="font-size: 60px">{{ msg }}</span><br />-->
      <!--<span style="font-size: 60px">{{ msgChina }}</span><br />-->
      <!--<span style="font-size: 40px">{{create}}</span>-->
    <!--</div>-->
    <div>
    <el-carousel :interval="4000" height="400px">
      <el-carousel-item v-for="item in ingList" :key="item">
        <img :src="item.src"class="image">
        <p>{{item.txt}}</p>
      </el-carousel-item>
    </el-carousel>
    </div>
    <div style="width: 29%; float:left;margin-left: 6px" >
      <el-collapse accordion>
        <el-collapse-item>
          <template slot="title">
             Consistency<i class="header-icon el-icon-info"></i>
          </template>
          <div></div>
          <div></div>
        </el-collapse-item>
        <el-collapse-item title=" Feedback">
          <div></div>
          <div></div>
        </el-collapse-item>
        <el-collapse-item title=" Efficiency">
          <div></div>
          <div></div>
          <div></div>
        </el-collapse-item>
        <el-collapse-item title=" Controllability">
          <div></div>
          <div></div>
        </el-collapse-item>
      </el-collapse>
    </div>
    <div style="width: 70%;float:left">
      <el-row>
      <el-col :span="8" v-for="item in ingList" :key="item" :offset="index > 0 ? 2 : 0">
        <el-card :body-style="{ padding: '0px' }">
          <img :src="item.src" class="image"/>
          <div style="padding: 14px;">
            <span></span>
            <div class="bottom clearfix">
              <time class="time">{{ currentDate }}</time>
              <el-tooltip content="Top center" placement="top">
              <el-button type="text" class="button" @click="dialogVisible = true"></el-button>
              </el-tooltip>
              <el-dialog
                title=""
                :visible.sync="dialogVisible"
                width="30%"
                :before-close="handleClose">
                <span></span>
                <span slot="footer" class="dialog-footer">
                  <el-button @click="dialogVisible = false"> </el-button>
                  <el-button type="primary" @click="dialogVisible = false"> </el-button>
                </span>
              </el-dialog>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>
    </div>
  </div>
</template>

<style>
	.blueFont{color:#409EFF;text-align: center;font-size: 24px;font-weight:bold;font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","",Arial,sans-serif;}
	.redFont{color:#F00;text-align: center;font-size: 24px;font-weight:bold;font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","",Arial,sans-serif;}
	.blackFont{color:#303133;text-align: center;font-size: 24px;font-weight:bold;font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","",Arial,sans-serif;background-image: url("../assets/b1.png")}
  .whiteFont{color:#fff;text-align: center;font-size: 24px;font-weight:bold;font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","",Arial,sans-serif;background-image: url("../assets/b1.png")}

  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 200px;
    margin: 0;
  }

  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;

  }

  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }
  .time {
    font-size: 13px;
    color: #999;
  }

  .bottom {
    margin-top: 13px;
    line-height: 12px;
  }

  .button {
    padding: 0;
    float: right;
  }

  .image {
    width: 100%;
    display: block;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }

  .clearfix:after {
    clear: both
  }
</style>
<script>
export default {
  name: 'hello',
  data () {
    return {
      msg: 'Welcome to My First App',
      msgChina: 'App',
      create:"Created by ZD",
      ingList:[
      	{src: require('../assets/1.jpg')},
      	{src: require('../assets/2.jpg')},
      	{src: require('../assets/3.jpg')},
      	{src: require('../assets/4.jpg')},
      	{src: require('../assets/5.jpg')},
      	{src: require('../assets/6.jpg')},
      	{src: require('../assets/7.jpg')},

      ],
      currentDate: new Date(),
      dialogVisible: false
    }
  },
  methods: {
    handleClose(done) {
      this.$confirm('')
        .then(_ => {
          done();
        })
        .catch(_ => {});
    }
  }
}
</script>









